<template>
  <VueMarkdown :if="markdown" :class="className">{{markdown}}</VueMarkdown>
</template>
<script>
import VueMarkdown from 'vue-markdown'

export default {
  name: 'MarkdownContent',
  props: {
    markdown: {
      type: String
    },
    className: {
      type: String
    }
  },
  components: {
    VueMarkdown
  }
}
</script>
<style lang="stylus" scoped></style>



